<template>
    <v-container>
        <div v-if="isLoad">
            <v-row>
                <v-col
                    lg="12"
                    sm="12"
                    md="12"
                >
                    <v-card 
                    max-width="100%"
                    style="margin-bottom: 20px;">
                        <v-card-title>{{ getEvents.mainTitle }}</v-card-title>
                        <v-img height="300" 
                        :src="'/images/' + getEvents.image"
                        ></v-img>
                        <v-card-text v-if="getEvents.mainDesc" style="text-align: justify;">
                            {{ getEvents.mainDesc.substr(0, 350) }}...    
                        </v-card-text>
                        <v-card-actions>
                            <v-btn 
                            outlined 
                            rounded 
                            class="text-xs-center"
                            @click="getEvent(getEvents.id)"
                        >Перейти</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
            </div>
    </v-container>

</template>

<script>
    import {mapGetters} from 'vuex'
    export default {
        data() {
            return {
                events: [],
                date: [],
                dateFilt: [],
                show: true,
                isLoad: false
            }
        },

        computed: {
            ...mapGetters(['getEvents']),
            getUrl() {
                return this.$store.state.url
            }
        },

        async mounted() {

            if (!this.$store.state.user || Object.keys(this.$store.state.user).length == 0) {
            this.$fire({
                title: "Уведомление",
                text: "Необходимо авторизоваться!",
                type: "error",
            })
            .then(this.$router.push('/howtouse'));
        }

            console.log("URL " + this.$store.state.getUrl)

            document.title = 'Технополис "ЭРА" | Программа';
            this.$emit('update:title', 'Программа')

            this.isLoad = await this.load()
            console.log(this.isLoad)
        },

        methods: {
            async load() {
                await this.$store.dispatch('getEvents')

                // await this.sortEvents()

                // this.getEvents.events.forEach(async elem => {
                //     await this.date.push(JSON.stringify({
                //         date: new Date(elem.timeStart).toLocaleDateString(),
                //         events: []
                //     }))
                // })

                // this.date = await Array.from(new Set(this.date))

                // this.date.forEach(async elem => {
                //     await this.dateFilt.push(JSON.parse(elem))
                // })

                // await this.addEvents()
                // await this.getBlue()
                return true
            },

            async getEvent(id) {
                this.$router.push({ path: `/event/${id}` })
            },

            async sortEvents() {
                this.getEvents.events = await this.getEvents.events.sort(function (a, b) {
                    if (a.timeStart > b.timeStart) {
                        return 1;
                    }
                    if (a.timeStart < b.timeStart) {
                        return -1;
                    }
                    return 0;
                });
            },

            async getBlue() {
              for (let i = 0; i < this.getEvents.events.length; i++) {
                  if (Number(new Date(this.getEvents.events[i].timeStart)) < Date.now() && Number(new Date(this.getEvents.events[i].timeStop)) > Date.now()) {
                      this.getEvents.events[i].isActive = 1
                  }
                  else if (Number(new Date(this.getEvents.events[i].timeStop)) < Date.now()) {
                    this.getEvents.events[i].isActive = 2;
                  }
                  else if (Number(new Date(this.getEvents.events[i].timeStart)) > Date.now()) {
                    this.getEvents.events[i].isActive = 0;
                  }
              }
          },

            async addEvents() {
                for (let i = 0; i < this.getEvents.events.length; i++) {
                    for(let j = 0; j < this.dateFilt.length; j++) {
                        if (this.dateFilt[j].date == new Date(this.getEvents.events[i].timeStart).toLocaleDateString()) {
                            this.dateFilt[j].getEvents.events.push(this.getEvents.events[i])
                        }
                    }
                }
            },

            getWeekDay(date) {
                let days = ['ВС', 'ПН', 'ВТ', 'СР', 'ЧТ', 'ПТ', 'СБ'];
                return days[new Date(date).getDay() + 1];
            }
        }
    }
</script>

